<template>
  <v-dialog
    v-if="plans"
    v-model="show"
    max-width="493"
    class="dolphin-custom-scrollbar"
  >
    <v-card class="tariff-form">
      <v-card-title v-html="$tc('personalArea.overview.tariffForm.title', 'base', { tariff: tariffName })" />
      <v-card-text>
        <v-list-item class="tariff-form-item">
          <span>{{$t('personalArea.overview.tariffForm.tariff')}}</span>
          <v-select
            :items="plans"
            item-text="name"
            item-value="id"
            outlined
            class="select-tariff"
            :value="currentPlan"
            @change="changePlan"
            return-object
          >
            <template v-slot:append>
              <v-icon>expand_more</v-icon>
            </template>
          </v-select>
        </v-list-item>

        <v-divider/>

        <v-list-item class="tariff-form-item">
          <span>{{$t('personalArea.overview.tariffForm.prolongMonths')}}</span>
          <v-select
            :items="availableDateList"
            item-text="text"
            item-value="month"
            outlined
            class="select-tariff"
            :value="currentDateData"
            @change="changeMonth"
            return-object
          >
            <template v-slot:append>
              <v-icon>expand_more</v-icon>
            </template>
          </v-select>
        </v-list-item>

        <v-divider/>

        <v-list-item class="tariff-form-item">
          <span>{{$t('personalArea.overview.tariffForm.promocode')}}</span>
          <div>
            <v-text-field
              outlined
              dense
              @focusout="handlePromocode"
              :error="hasPromocodeError"
            />
            <span
              v-if="hasPromocodeError"
              class="tariff-form-item-error"
            > {{$t('personalArea.overview.tariffForm.invalidPromocode')}} </span>
          </div>
        </v-list-item>

        <v-divider/>

        <v-list-item class="tariff-form-item">
          <span>{{$t('personalArea.overview.tariffForm.discount')}}</span>
          <div class="promocode">
            <v-icon
              v-if="promocodeIsLoading"
              :size="12"
              color="primary"
              class="mr-1"
            >fas fa-spinner fa-pulse</v-icon>
          </div>
          <div class="promocode" v-if="!promocodeIsLoading">{{amountData.discounted_amount}}$</div>
        </v-list-item>

        <v-divider/>

        <v-list-item class="tariff-form-item">
          <span class="payment">{{$t('personalArea.overview.tariffForm.payment')}}</span>
          <div class="promocode">
            <v-icon
              v-if="promocodeIsLoading"
              :size="12"
              color="primary"
              class="mr-1"
            >fas fa-spinner fa-pulse</v-icon>
          </div>
          <div class="promocode payment" v-if="!promocodeIsLoading">{{ payAmount }}$</div>
        </v-list-item>

        <div class="payment-buttons">
          <v-btn
            v-bind:class="{ active_payment: payment === 'cryptadium'}"
            outlined
            color="primary"
            @click="() => changePayment('cryptadium')"
          >CRYPTO</v-btn>

          <v-btn
            v-bind:class="{ active_payment: payment === 'capitalist'}"
            outlined
            color="primary"
            @click="() => changePayment('capitalist')"
          >CAPITALIST</v-btn>
        </div>

        <crypto-payment
          v-if="payment === 'cryptadium'"
          :disabled="isDisablePay"
          @pay="handlePay"
        />

        <capitalist-payment
          v-else
          :disabled="isDisablePay"
          @pay="handlePay"
        />
      </v-card-text>
    </v-card>
  </v-dialog>
</template>
<script>

import { upperCaseFirstLatter } from '@/helpers/StringHelper';
import {
  availableMonthList, PAYMENT_TYPE_CAPITALIST,
  PAYMENT_TYPE_CRYPTO,
} from '@/constants/tiktok/personalArea/payment/payment';
import CryptoPayment from '@/components/tiktok/personalArea/payment/dialogforms/CryptoPayment';
import CapitalistPayment from '@/components/tiktok/personalArea/payment/dialogforms/CapitalistPayment';
import { mapGetters } from 'vuex';

export default {
  name: 'TariffForm',

  data: () => ({
    hasPromocodeError: false,
    currentPlan: '',
    currentDate: { month: 1, percent: 0 },
    promocode: '',
    payment: PAYMENT_TYPE_CRYPTO,
    plans: '',
    amountData: {
      discounted_amount: 0,
      pay_amount: 0
    },
    promocodeIsLoading: false,
    isSubscribeProcessing: false,
  }),

  props: {
    isOpenPayForm: {
      type: Boolean,
      default: false
    },
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
    }),

    isDisablePay() {
      return this.hasPromocodeError || this.promocodeIsLoading || this.isSubscribeProcessing;
    },

    payAmount() {
      let percent = this.payment === PAYMENT_TYPE_CAPITALIST ? 5 : 0;
      let amount;

      if (this.promocode) {
        amount = this.amountData.pay_amount;
      } else {
        amount = this.currentPlan.price * this.currentDate.month;
      }

      amount = amount + (amount * percent / 100);

      return Math.round( amount * 100 ) / 100;
    },

    tariffName() {
      return upperCaseFirstLatter(this.currentPlan.name);
    },

    currentDateData() {
      let percent = this.currentDate.percent ? `- ${this.currentDate.percent}'%'` : '';

      return {
        ...this.currentDate,
        text: `${this.currentDate.month} ${this.$t('common.month')} ${percent}`

      }
    },

    availableDateList() {
      return availableMonthList.map(item => {
        let percent = item.percent ? `- ${item.percent}'%'` : '';

        return ({
          ...item,
          text: `${item.month} ${this.$t('common.month')} ${percent}`
        });
      })
    },

    show: {
      get() {
        return this.isOpenPayForm;
      },

      set(value) {
        this.$emit('close', value);
      }
    },
  },

  methods: {
    changeMonth(data) {
      this.currentDate = data;

      if (this.promocode) {
        this.getPromocode();
      }
    },

    changePlan(value) {
      this.currentPlan = value;

      if (this.promocode) {
        this.getPromocode();
      }
    },

    changePayment(value) {
      this.payment = value;
    },

    getPromocode() {
      let data = {
        plan_id: this.currentPlan.id,
        cycle_count: this.currentDate.month,
        promo_code: this.promocode
      };

      this.promocodeIsLoading = true;

      this.api.post(`payment/use-promocode`, data)
        .then(res => {
          if (res.data) {
            this.amountData = res.data;
            this.hasPromocodeError = false;
          } else {
            throw new Error('Invalid promocode')
          }
        }).catch(() => {
          this.hasPromocodeError = true;
        }).finally(() => {
          this.promocodeIsLoading = false;
        })
    },

    handlePromocode(e) {
      if (!e.target.value) {
        this.promocode = e.target.value;
        this.amountData = {
          discounted_amount: 0,
          pay_amount: 0
        };
        this.hasPromocodeError = false;

        return;
      }
      if (e.target.value !== this.promocode) {
        this.promocode = e.target.value;
        this.getPromocode();
      }
    },

    handlePay(currency) {
      if (this.promocodeIsLoading || this.hasPromocodeError) {
        return;
      }

      let data = {
        cycle_count: this.currentDate.month,
        promo_code: this.promocode,
        payment_method: this.payment,
        currency_type: currency
      };

      this.isSubscribeProcessing = true;

      this.api.post(`plans/${this.currentPlan.id}/subscribe`, data)
        .then(res => {
          if (res.data){
            if (this.payment === PAYMENT_TYPE_CRYPTO) {
              window.open(res.data,'_blank');
            } else {
              window.location.href = res.data;
            }
          }
        })
        .finally(() => this.isSubscribeProcessing = false);
    },

    getPlans() {
      this.api(`plans`).then(res => {
        if (res.data.data) {
          this.plans = res.data.data;
        }
      })
    }
  },

  mounted() {
    this.payment = this.profile.subscription.payment_service_type;
    this.currentPlan = this.profile.subscription.plan;
    this.getPlans();
  },

  components: {
    CapitalistPayment,
    CryptoPayment
  },
}
</script>

