<template>
  <div>
    <v-card>
      <v-card-text style="padding: 0px 8px 0px 8px">
        <!-- ТАБЛИЦА -->
        <v-data-table
          id="mainTableProxies"
          v-model="proxy.selected"
          :headers="cols"
          :items="proxy.filtered"
          :loading="loading.mainTable"
          :loading-text="$t('common.loading')"
          :page="page"
          :items-per-page="itemsPerPage"
          fixed-header
          :custom-sort="customSortsProxies"
          :header-props="{'sort-icon': 'arrow_downward' }"
          hide-default-footer
          :height="innerHeight - 50 - 48 - 4 - 16"
          show-select
          class="stat-table stat-table-proxies"
          :mobile-breakpoint="0"
          @update:items-per-page="setItemsPerPage($event)"
          @page-count="pageCount = $event"
        >
          <!-- LOG -->
          <template #[`item.log`]="{item}">
            <v-tooltip
              v-if="item.change_ip_url"
              bottom
            >
              <template v-slot:activator="{ on }">
                <v-btn
                  small
                  color="primary"
                  text
                  icon
                  style="min-width: 0; width: 30px; margin-top: 1px;"
                  v-on="on"
                  @click="openLog(item)"
                >
                  <v-icon :size="18">
                    assignment
                  </v-icon>
                </v-btn>
              </template>
              <span>{{ $t('common.logView') }}</span>
            </v-tooltip>
          </template>

          <!-- НАЗВАНИЕ -->
          <template #[`item.name`]="{item}">
            <div
              style="cursor: pointer; color: #2196F3; text-decoration: underline;"
              @click="$store.dispatch('proxy/setForEdit', item); $store.dispatch('proxy/openDialog', 'edit');"
            >
              {{ item.name || $t('common.noName') }}
            </div>
            <p class="text--disabled">
              {{ item.id }}
            </p>
            <div
              v-if="item.permissions"
              class="caption"
            >
              <a @click="$store.dispatch('proxy/setForPermissions', item); $store.dispatch('proxy/openDialog', 'permissions');">
                {{ $t('common.shared').toLowerCase() }} ({{ item.permissions.length }})
              </a>
            </div>
          </template>
          
          <!-- ТИП -->
          <template #[`item.type`]="{item}">
            {{ item.type.toUpperCase() }}
          </template>

          <!-- ХОСТ -->
          <template #[`item.host`]="{item}">
            {{ item.host }}
          </template>

          <!-- ВНЕШНИЙ IP -->
          <template #[`item.external_ip`]="{item}">
            <span v-if="item.external_ip">
              {{ item.external_ip }}
            </span>
            <span v-else>
              -
            </span>
          </template>
          
          <!-- СТАТУС -->
          <template #[`item.status`]="{item}">
            <v-chip
              v-if="item.status == 'active'"
              color="success"
              x-small
              label
              outlined
              class="mr-2"
              style="font-weight: bold;"
            >
              {{ $t('proxy.active').toUpperCase() }}
            </v-chip>

            <v-chip
              v-if="item.status == 'error'"
              color="red"
              x-small
              label
              outlined
              class="mr-2"
              style="font-weight: bold;"
            >
              {{ $t('proxy.error').toUpperCase() }}
            </v-chip>

            <v-chip
              v-if="item.status == 'new'"
              color="orange"
              x-small
              label
              outlined
              class="mr-2"
              style="font-weight: bold;"
            >
              {{ $t('proxy.new').toUpperCase() }}
            </v-chip>
          </template>

          <!-- АККАУНТЫ -->
          <template #[`item.accounts`]="{item}">
            <accounts :item="item" />
          </template>

          <!-- ПОСЛЕДНЯЯ ПРОВЕРКА -->
          <template #[`item.last_check`]="{item}">
            <span v-if="item.last_check">
              {{ item.last_check }}
            </span>
            <span v-else>
              -
            </span>
          </template>

          <!-- СРОК ДЕЙСТВИЯ -->
          <template #[`item.expiration_date`]="{item}">
            <span
              v-if="item.expires"
              :class="{'proxy-main-table-proxy-expires-soon': dateNow(item) }"
            >
              <v-icon
                v-if="dateNow(item)"
                style="color: #F44336;"
                :size="12"
                class="mr-1"
              >
                fas fa-exclamation
              </v-icon>
              {{ item.expires_text }}
            </span>
            <span v-else>
              -
            </span>
          </template>
        </v-data-table>
        <div
          class="d-flex justify-center align-center dolphin-main-table-footer"
        >
          <!-- ПАГИНАЦИЯ -->
          <div
            style="white-space: nowrap;"
            class="mx-2"
          >
            {{ currentTableItems + ' ' + $t('common.of').toLowerCase() + ' ' + itemsCount }}
          </div>

          <div style="white-space: nowrap;">
            <!-- СТРЕЛКА ВЛЕВО -->
            <v-btn
              icon
              x-small
              :disabled="page === 1"
              @click="changePage(false)"
            >
              <v-icon :size="12">
                fas fa-chevron-left
              </v-icon>
            </v-btn>

            <!-- СТРАНИЦА -->
            <v-chip
              label
              color="primary"
              small
              class="mx-2"
            >
              {{ page }}
            </v-chip>

            <!-- СТРЕЛКА ВПРАВО -->
            <v-btn
              icon
              x-small
              :disabled="page === pageCount"
              @click="changePage(true)"
            >
              <v-icon :size="12">
                fas fa-chevron-right
              </v-icon>
            </v-btn>
          </div>

          <v-select
            :value="itemsPerPage"
            :items="[10, 30, 50, 100]"
            hide-details
            dense
            solo
            class="dolphin-main-table-footer-items-per-page"
            :class="{'dolphin-main-table-footer-items-per-page-mobile': $vuetify.breakpoint.sm || $vuetify.breakpoint.xs}"
            style="max-width: 60px;"
            @change="setItemsPerPage($event)"
          />
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import {mapActions, mapGetters}                         from 'vuex';

import customSort                                       from '../../mixins/adsmanager/custom_sort';

import Accounts                                         from './ProxyMainTableAccounts';

export default {
  name: 'ProxyMainTable',

  components: {
    Accounts
  },
  mixins: [
    customSort
  ],

  data(){
    return{
      itemsPerPage: 50,
    };
  },
  watch:{
  },
  computed: {
    ...mapGetters({
      proxy: 'proxy/proxy',
      loading: 'proxy/loading',
      innerHeight: 'main/innerHeight',
      profile: 'main/profile',
      pageCount: 'proxy/count',
      itemsCount: 'proxy/itemsCount',
      page: 'proxy/page',
    }),

    cols() {
      let cols = [];

      // cols.push({text: this.$t('common.id'), value: 'id', width: 48});
      cols.push({text: '', value: 'log', width: 50});
      cols.push({text: this.$t('common.name'), value: 'name'});
      cols.push({text: this.$t('common.type'), value: 'type'});
      cols.push({text: this.$t('common.host'), value: 'host'});
      cols.push({text: this.$t('common.port'), value: 'port'});
      cols.push({text: this.$t('common.login'), value: 'login'});
      cols.push({text: this.$t('common.password'), value: 'password'});
      cols.push({text: this.$t('common.externalIP'), value: 'external_ip'});
      cols.push({text: this.$t('proxy.accountsCount'), value: 'accounts_count'});
      cols.push({text: this.$t('common.status'), value: 'status'});
      cols.push({text: this.$t('common.accounts'), value: 'accounts', width: 100},);
      cols.push({text: this.$t('proxy.lastCheck'), value: 'last_check'});
      cols.push({text: this.$t('proxy.expirity'), value: 'expiration_date'});

      return cols;
    },

    currentTableItems() {
      if (this.itemsPerPage === -1 || this.itemsPerPage > this.proxy.filtered.length) {
        return this.proxy.filtered.length;
      }
      if (this.page === 1) {
        return `1 - ${this.itemsPerPage}`;
      }
      if (this.page === this.pageCount) {
        return `${(this.page * this.itemsPerPage) - (this.itemsPerPage - 1)} - ${this.proxy.filtered.length}`;
      }
      return `${(this.page * this.itemsPerPage) - (this.itemsPerPage - 1)} - ${this.page * this.itemsPerPage}`;
    }
  },
  methods: {
    ...mapActions('proxy',[
      'loadProxy'
    ]),
    async openLog(item) {
      let data = {
        name: item.name,
        id: item.id
      };
      await this.$store.dispatch('proxy/getIpChangeLog', data);
      this.$store.dispatch('proxy/openDialog', 'log');
    },

    setItemsPerPage(number) {
      localStorage.setItem('proxies-items-per-page-count', number);
      localStorage.setItem('proxies-items-page', '1');
      this.$store.dispatch('proxy/loadProxy');
      this.itemsPerPage = number;
      this.page = 1;
    },

    changePage(next) {
      let page = 1;
      if(next) {
        page = this.page = this.page + 1;
      } else {
        page = this.page = this.page - 1;
      }
      this.$store.dispatch('proxy/setPage', page);
      this.$store.dispatch('proxy/loadProxy');
    },

    dateNow(item) {
      return item.expires - Date.now() / 1000 < 60 * 60 * 24 * 3;
    }
  }
};
</script>