<template>
  <v-dialog
    v-model="show"
    width="493"
    persistent
  >
    <v-card class="upgrade-with-support">
      <v-card-title class="upgrade-with-support-header">
        <v-icon
          color="primary"
          size="18px"
          @click="back"
        >fas fa-arrow-left</v-icon>
        <span>{{ $t('common.back').toUpperCase() }}</span>
      </v-card-title>
      <v-card-text class="upgrade-with-support-content">
        <v-img src="@/assets/images/smile.svg" />
        <div class="text">
          <div v-html="$t('personalArea.overview.tariffForm.upgradeWithSupport')" />
          <a href="https://t.me/dolphin_support_bot" class="support-link">support</a>
        </div>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'UpgradeWithSupport',

  props: {
    isOpenSupportForm: {
      type: Boolean,
      default: false
    },
  },

  computed: {
    show: {
      get() {
        return this.isOpenSupportForm;
      },

      set(value) {
        this.$emit('back', value);
      }
    },
  },

  methods: {
    back() {
      this.show = false;
    }
  }
}
</script>

