<template>
<div class="crypto-payment">
  <v-list-item
    v-for="(item, index) in cryptoPaymentList"
    :key="index"
    class="crypto-payment-item"
    @click="$emit('pay', item.currency )"
    :style="`color:${disabled ? '#CDCDCD!important': ''}` "
  >
    <div>
      <v-img :src="getImage(item.type)" />
      <span>{{item.type}}</span>
    </div>
    <span>{{item.value}}</span>
  </v-list-item>
</div>
</template>

<script>
import {
  CRYPTO_ITEM_TYPE_BITCOIN,
  CRYPTO_ITEM_TYPE_DOGECOIN,
  CRYPTO_ITEM_TYPE_ETHEREUM,
  cryptoPayments
} from "@/constants/tiktok/personalArea/payment/payment";

export default {
  name: 'CryptoPayment',

  data: () => ({
    cryptoPaymentList: cryptoPayments
  }),

  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },

  methods: {
    getImage(value) {
      switch (value) {
        case CRYPTO_ITEM_TYPE_BITCOIN:
          return require('@/assets/images/payment/bitcoin.svg');
        case CRYPTO_ITEM_TYPE_ETHEREUM:
          return require('@/assets/images/payment/ethereum.svg');
        case CRYPTO_ITEM_TYPE_DOGECOIN:
          return require('@/assets/images/payment/dogecoin.svg');
        default:
          return require('@/assets/images/payment/tether.svg');
      }
    }
  }
}
</script>

<style scoped>
.crypto-payment {
  display: flex;
  flex-direction: column;
}

.crypto-payment .crypto-payment-item {
  display: flex;
  justify-content: space-between;
  border: 1px solid #CDCDCD;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 8px 16px;
  margin: 4px 0;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
  cursor: pointer;
  min-height: unset;
}
.crypto-payment-item-active {
  color: red !important;
}

.crypto-payment > div > div {
  display: flex;
  align-items: center;
  width: 100%;
}

.crypto-payment > div > div span {
  margin-left: 16px;
}

.crypto-payment .v-image {
  height: 24px;
