<template>
  <v-dialog
    v-model="dialogs.log"
    max-width="800px"
    scrollable
  >
    <v-card>
      <!-- ЗАГОЛОВОК -->
      <v-card-title>
        <span class="headline">
          {{ $t('proxy.ipChangeLog') + ' [' + ipChangeLog.name + ']' }}
        </span>
      </v-card-title>

      <v-card-text class="pb-0">
        <v-data-table
          :headers="cols"
          :custom-sort="customSort"
          :items="ipChangeLog.log"
          :sort-desc="true"
          :options="{sortBy: ['time_added_text'], sortDesc: [false]}"
          :header-props="{'sort-icon': 'arrow_downward'}"
          :footer-props="{'items-per-page-options': [10]}"
        >
          <!-- ДЛИТЕЛЬНОСТЬ -->
          <template #item.time_exec="{item}">
            {{ item.time_exec + ' ' + $t('common.sec') }}
          </template>
        </v-data-table>
      </v-card-text>

      <v-card-actions>
        <v-spacer />
        <v-btn
          color="blue darken-1"
          text
          @click="$store.dispatch('proxy/closeDialog', 'log')"
        >
          {{ $t('common.close') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters}             from 'vuex';

export default {
  name: 'ProxyDialogLog',

  computed: {
    ...mapGetters({
      dialogs: 'proxy/dialogs',
      ipChangeLog: 'proxy/ipChangeLog'
    }),

    cols() {
      let cols = [];

      cols.push({text: this.$t('common.date'), value: 'time_added_text'});
      cols.push({text: this.$t('common.account'), value: 'account.name', width: 150, sortable: false});
      cols.push({text: this.$t('common.oldIp'), value: 'old_ip', width: 150, sortable: false});
      cols.push({text: this.$t('common.newIp'), value: 'new_ip', width: 150, sortable: false});
      cols.push({text: this.$t('common.duration'), value: 'time_exec', width: 150, sortable: false});

      return cols;
    }
  },

  methods: {
    customSort(items, sortBy, sortDesc) {
      if (sortBy[0] === 'time_added_text' && sortDesc[0]) {
        return items.concat().sort((a, b) => a.time_added - b.time_added);
      }
      if (sortBy[0] === 'time_added_text' && !sortDesc[0]) {
        return items.concat().sort((a, b) => b.time_added - a.time_added);
      }
      return items;
    }
  }
};
</script>
