<template>
  <v-dialog
    :value="dialogs.share"
    max-width="500px"
    scrollable
    @click:outside="close"
    @keydown.esc="close"
  >
    <v-card :loading="loading.share">
      <!-- ЗАГОЛОВОК -->
      <v-card-title>
        <span class="headline">
          {{ $t('dialogs.share.title') }}
        </span>
      </v-card-title>

      <!-- ОКНО -->
      <v-card-text style="max-height: 700px;">
        <v-container>
          <v-row>
            <v-col
              cols="12"
            >
              <div>
                <!-- ВЫБРАТЬ ВСЕ -->
                <v-btn
                  color="primary"
                  x-small
                  style="margin-left: 0px;"
                  class="my-1"
                  outlined
                  @click="selectAll"
                >
                  {{ $t('common.selectAll') }}
                </v-btn>

                <!-- УБРАТЬ ВСЕ -->
                <v-btn
                  color="primary"
                  x-small
                  style="margin-left: 0px;"
                  class="my-1 ml-1"
                  outlined
                  @click="unselectAll"
                >
                  {{ $t('common.unselectAll') }}
                </v-btn>
              </div>

              <!-- ДЕРЕВО ПОЛЬЗОВАТЕЛЕЙ -->
              <v-treeview
                v-model="permissions"
                selectable
                dense
                open-on-click
                selected-color="primary"
                :items="items"
              >
                <template v-slot:prepend="{ item }">
                  <!-- ИКОНКА - ПОЛЬЗОВАТЕЛЬ -->
                  <v-icon
                    v-if="item.id.search('user') !== -1"
                    size="16"
                    class="d-block text-center"
                  >
                    fas fa-user
                  </v-icon>

                  <!-- ИКОНКА - ПОЛЬЗОВАТЬСЯ -->
                  <v-icon
                    v-if="item.id.search('usage') !== -1"
                    size="16"
                    class="d-block text-center"
                  >
                    fas fa-eye
                  </v-icon>

                  <!-- ИКОНКА - ДЕЙСТВИЯ -->
                  <v-icon
                    v-if="item.id.search('actions') !== -1"
                    size="16"
                    class="d-block text-center"
                  >
                    fas fa-bolt
                  </v-icon>

                  <!-- ИКОНКА - РЕДАКТИРОВАТЬ -->
                  <v-icon
                    v-if="item.id.search('edit') !== -1"
                    size="16"
                    class="d-block text-center"
                  >
                    fas fa-pencil-alt
                  </v-icon>
                </template>
              </v-treeview>

              <!-- ВЫБОР РЕЖИМА -->
              <div>
                <v-radio-group
                  v-model="mode"
                  row
                  dense
                  style="margin-top: 0px;"
                  hide-details
                  class="mt-3"
                >
                  <v-radio
                    :label="$t('dialogs.share.addPermissions')"
                    :value="1"
                  />
                  <v-radio
                    :label="$t('dialogs.share.removePermissions')"
                    :value="-1"
                  />
                </v-radio-group>
              </div>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          color="blue darken-1"
          text
          @click="close"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          color="blue darken-1"
          text
          @click="savePermissions"
        >
          {{ $t('common.save') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'ProxyDialogShare',

  data: () => ({
    permissions: [],
    mode: 1,
    allItems: [],
    users: [],
  }),

  computed: {
    ...mapGetters({
      proxy: 'proxy/selected',
      // users: 'users/allUsers',
      dialogs: 'proxy/dialogs',
      loading: 'proxy/loading',
      profile: 'main/profile',
    }),

    items() {
      let items = [];

      this.users.forEach(user =>  {
        if (user.id === this.profile.id) return;

        let item = {
          id: user.id,
          name: user.display_name !== null ? user.display_name : user.username
        };

        // item.children = [
        //   { id: `${user.id}-usage`, name: this.$t('dialogs.share.usage') },
        //   { id: `${user.id}-edit`, name: this.$t('dialogs.share.edit') },
        //   { id: `${user.id}-actions`, name: this.$t('dialogs.share.actions') },
        // ];

        // this.allItems = this.allItems.concat(item.children.map(child => child.id));
        this.allItems = this.allItems.concat(item);

        items.push(item);
      });

      return items;
    }
  },

  created() {
    this.loadUsers();
  },
        
  methods: {
    close() {
      if (this.permissions.length !== 0 || this.mode === -1) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('proxy/closeDialog', 'share');
    },

    async savePermissions() {
      const success = await this.$store.dispatch('proxy/savePermissions', {
        ids: this.proxy.map(proxy => proxy.id),
        permissions: this.permissions,
        mode: this.mode,
      });
      if (success) {
        this.$store.dispatch('proxy/closeDialog', 'share');
      }
    },

    selectAll() {
      this.permissions = [];
      this.permissions = this.permissions.concat(this.allItems);
    },

    unselectAll() {
      this.permissions = [];
    },

    async loadUsers() {
      const response = await this.cloudApi('users');
      if (typeof response.data.data !== 'undefined') {
        this.users = response.data.data;
      }
    }
  }
};
</script>