<template>
  <div>
    <v-card class="overview">
      <v-card-text>
        <div class="overview_item">
          <span class="overview_item_title">{{ $t('personalArea.overview.accountEmail') }}</span>
          <span class="overview_item_info">{{ profile.username }}</span>
        </div>
        <v-divider />
        <div class="overview_item">
          <span class="overview_item_title">{{ $t('personalArea.overview.plan') }}</span>
          <div class="plan-name">
            <div v-if="profile.subscription" class="overview_item_info">
              <span>{{ planName }}</span>
              <span v-if="isExpired"> {{ $t('common.expired') }}</span>
            </div>
            <v-btn
              v-if="isUpgrade"
              rounded
              color="primary"
              class="renew"
              @click="handleUpgrade"
            >
              {{ $t('common.upgrade') }}
            </v-btn>
          </div>
        </div>
        <v-divider />
        <div class="overview_item">
          <span class="overview_item_title">{{ $t('personalArea.overview.expires') }}</span>

          <div>
            <span class="overview_item_info">{{ expiredDate }}</span>
            <v-btn
              v-if="false"
              outlined
              rounded
              color="primary"
              class="renew"
            >
              {{ $t('common.renew') }}
            </v-btn>
          </div>
        </div>
        <v-divider />
      </v-card-text>
    </v-card>

    <tariff-form :is-open-pay-form="isOpenPayForm" @close="closePayForm"  v-if="profile.subscription" />
    <upgrade-with-support :is-open-support-form="isOpenSupportForm" @back="closeSupportForm" />
  </div>
</template>

<script>
import moment from 'moment';
import {
  PLAN_TYPE_BASE,
  PLAN_TYPE_TRIAL,
  SUBSCRIPTION_STATUS_EXPIRED,
} from '@/constants/tiktok/personalArea/payment/payment';
import { mapGetters } from 'vuex';
import TariffForm from "@/components/tiktok/personalArea/payment/dialogforms/TariffForm";
import UpgradeWithSupport from "@/components/tiktok/personalArea/payment/dialogforms/UpgradeWithSupport";


export default {
  name: 'Overview',

  data: () => ({
    isOpenSupportForm: false,
    isOpenPayForm: false,
  }),

  computed: {
    ...mapGetters({
      profile: 'main/profile',
    }),

    isExpired() {
      return this.profile.subscription.status === SUBSCRIPTION_STATUS_EXPIRED;
    },

    planName() {
      if (this.profile.subscription && this.profile.subscription.is_trial) {
        return PLAN_TYPE_TRIAL;
      }

      return this.profile.subscription.plan.name;
    },

    isUpgrade() {
      return true;
    },

    isRenew() {
      return this.paymentData.type === PLAN_TYPE_BASE;
    },

    expiredDate() {
      if (!this.profile.subscription) {
        return '';
      }

      return moment(this.profile.subscription.ends_at).format('DD MMM YYYY');
    }
  },

  methods: {
    handleUpgrade() {
      if (this.profile.subscription && this.profile.subscription.is_trial) {
        this.isOpenPayForm = true;
      } else {
        this.isOpenSupportForm = true;
      }
    },

    closeSupportForm() {
      this.isOpenSupportForm = false;
    },

    closePayForm() {
      this.isOpenPayForm = false;
    }
  },

  components: {
    UpgradeWithSupport,
    TariffForm
  },
}
</script>

<style scoped>
.v-card {
  box-shadow: unset;
}
.v-card__text {
  padding: 0 20px;
}
.overview .overview_item {
  display: flex;
  justify-content: space-between;
  min-height: 48px;
  /*padding: 16px 0;*/
  align-items: center;
}
