<template>
  <v-dialog
    v-model="dialogs.expirityDate"
    scrollable
    max-width="600px"
  >
    <v-card>
      <v-card-title>
        <span class="headline">
          {{ $t('proxy.expirityDate') }}
        </span>
      </v-card-title>
      <v-card-text>
        <v-menu
          v-model="datePicker"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          min-width="200px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="date"
              :label="$t('common.date')"
              class="proxy-dialog-expirity-date-datepicker"
              prepend-icon="event"
              readonly
              v-bind="attrs"
              v-on="on"
            />
          </template>
          <v-date-picker 
            v-model="date" 
            no-title 
            @input="datePicker = false" 
          />
        </v-menu>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          color="blue darken-1"
          text
          @click="$store.dispatch('proxy/closeDialog', 'expirityDate')"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          color="blue darken-1"
          text
          @click="save"
        >
          {{ $t('common.save') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'ProxyDialogSetExpirityDate',

  data() {
    return {
      datePicker: false,
      date: new Date(new Date().setDate(new Date().getUTCDate()+30)).toISOString().substr(0, 10)
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'proxy/dialogs',
      proxy: 'proxy/proxy'
    })
  },

  methods: {
    async save() {
      for (let selectedProxy of this.proxy.selected) {
        let data = {
          proxy_id: selectedProxy.id,
          expires: this.date
        };
        await this.api.patch('/proxy', data);
        // await this.api.post('/proxy/save_expirity', data);
      }
      await this.$store.dispatch('proxy/closeDialog', 'expirityDate');
      await this.$store.dispatch('proxy/loadProxy');
    }
  }
};
</script>