<template>
  <div style="float: left;">
    <!-- ПРОВЕРКА -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn
          small
          color="primary"
          text
          style="min-width: 0; width: 30px; margin-top: 1px;"
          :disabled="proxy.selected.length === 0"
          v-on="on"
          @click="$store.dispatch('proxy/openDialog', 'check')"
        >
          <v-icon :size="14">
            fas fa-check-double
          </v-icon>
        </v-btn>
      </template>
      <span>{{ $t('common.check') }}</span>
    </v-tooltip>

    <!-- ПОДЕЛИТЬСЯ -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn
          small
          color="primary"
          text
          style="min-width: 0; width: 30px; margin-top: 1px;"
          :disabled="proxy.selected.length === 0"
          v-on="on"
          @click="$store.dispatch('proxy/openDialog', 'share')"
        >
          <v-icon :size="14">
            fas fa-share-alt
          </v-icon>
        </v-btn>
      </template>
      <span>{{ $t('common.share') }}</span>
    </v-tooltip>

    <!-- УДАЛИТЬ -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn
          small
          color="primary"
          text
          style="min-width: 0; width: 30px; margin-top: 1px;"
          :disabled="proxy.selected.length === 0"
          v-on="on"
          @click="bulkDelete"
        >
          <v-icon :size="14">
            fas fa-trash-alt
          </v-icon>
        </v-btn>
      </template>
      <span>{{ $t('common.delete') }}</span>
    </v-tooltip>

    <!-- ДАТА ОКОНЧАНИЯ ПРОКСИ -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn
          small
          color="primary"
          text
          style="min-width: 0; width: 30px; margin-top: 1px;"
          :disabled="proxy.selected.length === 0"
          v-on="on"
          @click="$store.dispatch('proxy/openDialog', 'expirityDate')"
        >
          <v-icon :size="15">
            far fa-calendar-alt
          </v-icon>
        </v-btn>
      </template>
      <span>{{ $t('proxy.setExpirityDate') }}</span>
    </v-tooltip>

    <!-- СЧЕТЧИК -->
    <span
      class="body-2 ml-1"
      :style="{color: proxy.selected.length > 0 ? '' : 'gray'}"
    >
      {{ $t('common.selected') }} : {{ proxy.selected.length }}
    </span>
  </div>
</template>

<script>
import {mapGetters}          from 'vuex';

export default {
  name: 'ProxyTopbarActions',

  computed: {
    ...mapGetters({
      proxy: 'proxy/proxy',
    })
  },

  methods: {
    async bulkDelete() {
      try {
        this.loading = true;

        if (!confirm(this.$t('common.confirmPlease'))) {
          return;
        }

        const ids = this.proxy.selected.map(i => i.id);
        const response = await this.api.delete('/proxies', { params: { ids } });

        if (response.status === 200) {
          await this.$store.dispatch('proxy/loadProxy');
          await this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.deleted')
          });
          this.loading = false;
          this.$store.dispatch('proxy/clearSelected');
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        console.log(error);
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    }
  }
};
</script>
