    <v-row>
      <v-col
        cols="12"
        md="8"
        offset-md="2"
        lg="6"
        offset-lg="3"
      >
        <v-tabs vertical>
          <v-tab @click="() => changeTab($t('common.overview'))">
            {{ $t('common.overview') }}
          </v-tab>

          <v-tab @click="() => changeTab($t('common.affiliateProgram'))">
            {{ $t('common.affiliateProgram') }}
          </v-tab>

          <v-tab-item>
            <overview />
          </v-tab-item>

          <v-tab-item>
            <v-card flat>
              <v-card-text>
                <v-alert
                  text
                  v-html="$t('personalArea.affiliateText')"
                />
                <v-alert
                  v-if="profile && profile.referral_link"
                  text
                >
                  <v-row
                    align="center"
                    no-gutters
                  >
                    <v-col class="grow">
                      {{ profile.referral_link }}
                    </v-col>
                    <v-spacer />
                    <v-col class="shrink">
                      <v-btn
                        icon
                        @click="copyToClipboard(profile.referral_link)"
                      >
                        <v-icon
                          :size="14"
                        >
                          fas fa-copy
                        </v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs>
      </v-col>
    </v-row>
  </v-card>
