<script>
import Overview       from '@/components/tiktok/personalArea/payment/Overview';
import clipboard      from '@/mixins/clipboard';
import { mapGetters } from 'vuex';

export default {
  name: 'PersonalArea',

  mixins: [ clipboard ],

  data: function () {
    return {
      tabName: this.$t('common.overview')
    };
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
    }),
  },

  created() {
    if (this.profile && (!this.profile.referral_link  || !this.profile.subscription)) {
      this.$store.dispatch('main/loadProfile');
    }
  },

  methods: {
    changeTab(name) {
      this.tabName = name;
      console.log(name,'aaaaaaaaaaaaaaaaaaaaa');
    }
  },

  components: {
    Overview
  },
};
</script>

