<template>
  <v-dialog
    :value="true"
    max-width="400px"
    scrollable
    @click:outside="close"
    @keydown.esc="close"
  >
    <v-card :loading="false">
      <v-card-title>
        <div style="display: flex; justify-content: space-between; width: 100%;">
          <div>
            <span class="headline">
              {{ $t('dialogs.proxy.edit.title') }}
            </span>
          </div>
        </div>
      </v-card-title>
      <v-card-text style="max-height: 700px;">
        <v-container>
          <v-row>
            <!-- НАЗВАНИЕ -->
            <v-col
              cols="12"
            >
              <v-text-field
                :value="newProxy.name"
                :label="$t('common.name')"
                dense
                @input="clearInput($event, 'name')"
              />
            </v-col>

            <!-- ТИП -->
            <v-col cols="12">
              <v-autocomplete
                v-model="newProxy.type"
                dense
                :label="$t('common.type')"
                :items="['http', 'socks4', 'socks5']"
                :item-text="item => item.toString().toUpperCase()"
              />
            </v-col>

            <!-- ХОСТ -->
            <v-col
              cols="12"
            >
              <v-text-field
                v-model="newProxy.host"
                :label="$t('common.host')"
                dense
              />
            </v-col>

            <!-- ПОРТ -->
            <v-col
              cols="12"
            >
              <v-text-field
                v-model="newProxy.port"
                :label="$t('common.port')"
                dense
              />
            </v-col>

            <!-- ЛОГИН -->
            <v-col
              cols="12"
            >
              <v-text-field
                :value="newProxy.login"
                :label="$t('common.login')"
                dense
                @input="clearInput($event, 'login')"
              />
            </v-col>

            <!-- ПАРОЛЬ -->
            <v-col
              cols="12"
            >
              <v-text-field
                :value="newProxy.password"
                :label="$t('common.password')"
                dense
                @input="clearInput($event, 'password')"
              />
            </v-col>

            <!-- ССЫЛКА ДЛЯ СМЕНЫ IP -->
            <v-col
              cols="12"
            >
              <v-text-field
                :value="newProxy.change_ip_url"
                :label="$t('common.changeIPUrl')"
                dense
                @input="clearInput($event, 'change_ip_url')"
              />
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          color="blue darken-1"
          text
          @click="close"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          color="blue darken-1"
          text
          @click="$store.dispatch('proxy/saveProxy', newProxy); $store.dispatch('proxy/closeDialog', 'edit')"
        >
          {{ $t('common.save') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'ProxyDialogEdit',

  data() {
    return {
      newProxy: {},
      initial: {}
    };
  },

  computed: {
    ...mapGetters({
      proxy: 'proxy/forEdit'
    })
  },

  created() {
    this.newProxy = this.$_.cloneDeep(this.proxy);
    this.initial = this.$_.cloneDeep(this.proxy); 
  },

  methods: {
    close() {
      if (!this.$_.isEqual(this.newProxy, this.initial)) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('proxy/closeDialog', 'edit');
    },

    clearInput(event, property) {
      if (event === '') {
        this.newProxy[property] = null;
      } else {
        this.newProxy[property] = event;
      }
    }
  }
};
</script>